@value accentColor: $accentColor;
.iconContainer {
  display: -ms-inline-flexbox;
  display: inline-flex;
  outline: none; }

.onClick {
  cursor: pointer;
  -webkit-transition: 0.3s ease;
  -o-transition: 0.3s ease;
  transition: 0.3s ease; }
  .onClick:hover {
    color: currentColor;
    opacity: 0.7; }

.disabled {
  cursor: auto !important;
  color: #e4e4e4 !important; }
  .disabled svg {
    color: #e4e4e4 !important; }
  .disabled:hover {
    color: #e4e4e4 !important;
    opacity: 1; }
    .disabled:hover svg {
      color: #e4e4e4 !important;
      opacity: 1; }



